<template>
  <div id="all">
	  <div id="aside">
	  	<div id="header">
	  		<h1><router-link to="/book">图书管理</router-link></h1>
	  	</div>
	  	<div id="nav">
			<button class="btn"><router-link to="/"><span>首&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;页</span></router-link></button>
	  		<button class="btn"><router-link to="/stu"><span>学生管理</span></router-link></button>
			<button class="btn"><router-link to="/book"><span>图书管理</span></router-link></button>
	  		<button class="btn"><router-link to="/borrow"><span>借阅管理</span></router-link></button>
	  	</div>
	  </div>
	  <div class="top">
	  	<div class="btn-2">
	  		<button class="btn2"><router-link to="/uselogin"><span>用户登录</span></router-link></button>
	  		<button class="btn2"><router-link to="/uselogin"><span>退出</span></router-link></button>
	  	</div>
	  </div>

	  <div id="div3">
	  	<div class="query">
	  		<p class="p1">关键字查找:<input class="input1" type="text" placeholder="请输入关键字" v-model="keywords"></p>
	  	</div>
	  </div>
	  <div id="div1">
	  	<table class="table1">
	  		<tr>
	  			<td>序号</td>
	  			<td>书名</td>
	  			<td>作者</td>
				<td>位置</td>
	  			<td>价格</td>
	  			<td>操作</td>
	  		</tr>
	  		<tr v-for="(book,index) in books_result">
	  			<td>{{index+1}}</td>
	  			<td>{{book.name}}</td>
	  			<td>{{book.author}}</td>
				<td>{{book.location}}</td>
	  			<td>{{book.price}}</td>
	  			<td>
	  				<button class="button1" @click="updatebook(book)">修改</button> |
	  				<button class="button1" @click="delbook(index)">删除</button>
	  			</td>
	  		</tr>
	  		<tr>
	  			<td colspan="6">书本总数:{{total}}</td>
	  		</tr>
	  	</table>
	  
	  <table class="table2">
	  	<td>
	  		<p>添加/修改书籍</p>
	  		
	  		<p>书名:<input type="text" v-model="name" /></p>
	  		<p>作者:<input type="text" v-model="author" /></p>
			<p>位置:<input type="text" v-model="location" /></p>
	  		<p>价格:<input type="number" v-model="price" /></p>
	  		<p><button class="button1" @click="updbook()">修改</button>|<button class="button1" @click="add()">添加</button></p>
	  		
	  	</td>
	  </table>
	  </div>
  </div>
</template>

<script>
	export default {
		name:'book',
		data(){
			return{
				books:[],
				updatedBook: {},
				flag:false,
				name:"",
				author:"",
				price:"",
				keywords:"",
			}
		},
		created(){
			this.getBook()
		},
		methods:{
			getBook(){
				this.$http.get('http://127.0.0.1:9000/all')
				.then(res => {
					this.books = res.data.res
				})
			},
			
			delbook(index){
				if(window.confirm('确认要删除？')){
					// this.$http.delete('http://127.0.0.1:9000/delete')
					// .then(res => {
					// 	this.books = res.data.rd
					// })
					this.books.splice(index, 1);
				   }
			},
			add(){
				if(this.name.length != 0 && this.author.length !=0 && this.location.length !=0 && this.price.length !=0){
					var params = new URLSearchParams();
					params.append('name', this.name);
					params.append('author', this.author);
					params.append('location', this.location);
					params.append('price', this.price);
					this.$http.post('http://127.0.0.1:9000/add',params)
					.then(res => {
						console.log(res)
						this.books.push(params)
					})
				}else{
					alert("不能为空")
				}
				// var newbook = new Object();
				// 判断
				// if(this.name.length != 0 && this.author.length !=0 && this.location.length !=0 && this.price.length !=0){
				// 	newbook.name=this.name;
				// 	newbook.author=this.author;
				// 	newbook.location=this.location;
				// 	newbook.price=this.price;
				// 	// 把当前的数据放入数组中
				// 	this.books.push(newbook);
				// 	// 清空数据
				// 	this.name="";
				// 	this.author="";
				// 	this.location="",
				// 	this.price="";
				// }else{
				// 	alert("不能为空")
				// }
			},
			updatebook(book) {
				this.updatedBook = book;
				this.name = this.updatedBook.name;
				this.author = this.updatedBook.author;
				this.location = this.updatedBook.location;
				this.price = this.updatedBook.price;
				this.flag = true;
			},
			updbook() {
				this.updatedBook.name = this.name;
				this.updatedBook.author = this.author;
				this.updatedBook.location = this.location;
				this.updatedBook.price = this.price;
				
				this.flag = false;
				this.updatedBook = {};
				
				this.name = '';
				this.author = '';
				this.location ="",
				this.price = '';
			}
		},
		computed:{
		    total:function(){
		        return this.books.length;
		    },
			
			
			books_result() {
			  var _this = this;
			  if (this.keywords) {
			 //this的作用域到此结束，无法穿透进入filter函数,所以需要用一个变量（比如_this）来保存this的指向。
			
			    // 下面的参数item指的是其中一条图书的搜索记录
			    let querybooks = this.books.filter(function (item) {
			      if (item.name.indexOf(_this.keywords) >= 0 || item.author.indexOf(_this.keywords) >= 0) {
			        //找到输入的关键词对应的书名的索引，存在则>=0,不存在则返回-1
			        return true
			      } else {
			        return false
			      }
			    })
			
			    return querybooks
			  } else {
			    return this.books //没有关键词就显示所有的数据
			  }
			}
			
			
		},
	}
</script>


<style>
	@import url("../assets/css.css");
</style>
