<template>
	<div>
		<table border="2">
		    <tr v-for="users in userList">
		        <td>{{users.name}}</td>
		        <td>{{users.author}}</td>
				<td>{{users.location}}</td>
				<td>{{users.price}}</td>
		    </tr>
		 </table>
		<button @click="getUserList()">获取user数据</button>
		<button @click="getpost()">post按钮</button>
	</div>
</template>

 <script>
        export default({
            data(){//在data定义变量和初始值
                return{
					userList:[]
				}
            },
            // created(){//页面渲染之前执行，调用定义的方法
            //     //3.调用定义的方法
            //     this.getUserList()
            // },
            methods:{
				getpost(){
					var params = new URLSearchParams();
					params.append('name', '德莱文');
					params.append('author', 'manatao');
					params.append('location', 'B001');
					params.append('price', '30');
					this.$http.post('http://127.0.0.1:9000/add',params)
					.then(res => {
						console.log(res)
						
					})
				},
				//编写具体的方法
                getUserList(){
                  //2.使用axios发送ajax请求
                  //axios.提交方式(“请求s接口路径”).then(箭头函数).catch(箭头函数|)
                  this.$http.get('http://127.0.0.1:9000/query?name=三国演义')
					.then( res => {
						console.log(res)
						this.userList = res.data.res
						console.log(this.userList)
					})
                    .catch(error=>{
 
                    })
              }
            }
        })
    </script>


<style>
	@import url("../assets/css.css");
</style>



