<template>
	<fieldset>
	    <legend>用户登录系统</legend>
	    <div class="user">
	        账号：
	        <input type="text" placeholder="请输入账号" v-model="User.username">
	    </div>
	    <div class="user">
	        密码：
	        <input type="text" placeholder="请输入密码" v-model="User.userpwd">
	    </div>
		<p>
			<button @click="userlogin()">登录</button>
		</p>
	</fieldset>
</template>

<script>
	export default{
	    name:"uselogin",
	    data(){
			return{
				username: 'user',
				userpwd: '123',
				User:{username:'',userpwd:''},
			}
	    },
	    methods: { 
	        userlogin(){
	            if(this.User.username == 'user'&&this.User.userpwd == '123'){
	                alert("用户登录成功");
	                this.User = {username:'',userpwd:''};
	            }else{
	                alert("用户登录失败");
	            }
	        }
			
	    },
	};
</script>

