<template>
	<div id="all">
		<div id="aside">
			<div id="header">
				<h1><router-link to="/stuborrow">借阅图书</router-link></h1>
			</div>
			<div id="nav">
				<button class="btn"><router-link to="/borrow"><span>用户首页</span></router-link></button>
				<button class="btn"><router-link to="/stuborrow"><span>借阅图书</span></router-link></button>
			</div>
		</div>
		<div class="top">
			
		</div>
		<fieldset class="fieldset1">
			<legend class="legend1">借阅书籍</legend>
			<div class="table">
				<p>姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:<input type="text" v-model="name" /></p>
				<p>学&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号:<input type="text" v-model="sno" /></p>
				<p>班&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;级:<input type="text" v-model="banji" /></p>
				<p>借阅书籍:<input type="text" v-model="book" /></p>
				<p>
					<button class="btn1" type="button" @click="submit()">确认</button>
					<!-- <button type="button" @click="upd()">修改</button> -->
				</p>
			</div>
		</fieldset>
		
			<table class="table3">
				<th colspan="7">借阅申请</th>
				<tr>
					<td>序号</td>
					<td>姓名</td>
					<td>学号</td>
					<td>班级</td>
					<td>借阅书籍</td>
					<td>借阅时间</td>
					<td>操作</td>
				</tr>
				<tr v-for="(user,index) in users">
					<td>{{index+1}}</td>
					<td>{{user.name}}</td>
					<td>{{user.sno}}</td>
					<td>{{user.banji}}</td>
					<td>{{user.book}}</td>
					<td>{{user.date | dateformat}}</td>
					<td>
						<button class="button1" @click="delstu(index)">取消</button>
					</td>
				</tr>
			</table>
	</div>
</template>

<script>
		export default {
			name:"stuborrow",
			data(){
				return{
					flag:false,
					name:"",
					sno:"",
					banji:"",
					book:"",
					users:[],
				}
			},
			methods:{
				submit(){
					var newuser = new Object;
					if(this.name.length != 0 && this.sno.length != 0 && this.banji.length != 0 && this.book.length != 0){
						newuser.name = this.name;
						newuser.sno = this.sno;
						newuser.banji = this.banji;
						newuser.date=new Date();
						newuser.book = this.book;
						
						this.users.push(newuser);
						
						this.name="";
						this.sno="";
						this.banji="";
						this.book="";
					}else{
						alert('请填写完整');
					}
				},
				delstu(index){
					if(window.confirm('确认要删除？')){
						this.users.splice(index,1);
					}
				}
			},
			filters:{
				dateformat(val){
					return val.getFullYear()+"-"+(val.getMonth()+1)+"-"+val.getDate()
				}
			},
		}
	</script>

<style>
</style>
