import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import stu from './components/stu.vue'
import book from './components/book.vue'
import borrow from './components/borrow.vue'
import shouye from './components/shouye.vue'
import stuborrow from './user/stuborrow.vue'
import uselogin from './user/uselogin.vue'
import ceshibook from './components/ceshibook.vue'

Vue.use(Router)
// axios.defaults.baseURI = 'http://127.0.0.1:9000'
Vue.prototype.$http = axios;



const router = new Router({
	routes: [
		{
			path: '/ceshibook',
			component: ceshibook
		},
		{
			path: '/',
			component: shouye
		},
		{
			path: '/stu',
			component: stu
		},
		{
			path: '/book',
			component: book
		},
		{
			path: '/borrow',
			component: borrow
		},
		
		{
			path: '/uselogin',
			name:'/uselogin',
			component: uselogin
		},
		{
			path: '/stuborrow',
			component: stuborrow
		},
	]
})
export default router