<template>
	<div id="all">
		<div id="aside">
			<div id="header">
				<h1><router-link to="/borrow">借阅管理</router-link></h1>
			</div>
			<div id="nav">
				<button class="btn"><router-link to="/"><span>首&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;页</span></router-link></button>
				<button class="btn"><router-link to="/stu"><span>学生管理</span></router-link></button>
				<button class="btn"><router-link to="/book"><span>图书管理</span></router-link></button>
				<button class="btn"><router-link to="/borrow"><span>借阅管理</span></router-link></button>
			</div>
		</div>
		<div class="top">
			<div class="btn-2">
				<button class="btn2"><router-link to="/uselogin"><span>用户登录</span></router-link></button>
				<button class="btn2"><router-link to="/uselogin"><span>退出</span></router-link></button>
			</div>
		</div>
		<div id="div3">
			<div class="query">
				<p class="p1">关键字查找:<input class="input1" type="text" placeholder="请输入关键字" v-model="keywords"></p>
			</div>
		</div>
		<div id="div1">
			<table class="table1">
				<tr>
					<td>序号</td>
					<td>姓名</td>
					<td>学号</td>
					<td>借阅书本</td>
					<td>借阅日期</td>
					<td>归还日期</td>
					<td>是否归还</td>
					<td>操作</td>
				</tr>
				<tr v-for="(stu,index) in stus_result">
					<td>{{index+1}}</td>
					<td>{{stu.sname}}</td>
					<td>{{stu.sno}}</td>
					<td>{{stu.bbook}}</td>
					<td>{{stu.timeborrow}}</td>
					<td>{{stu.timereturn}}</td>
					<td>{{stu.state}}</td>
					<td>
						<button class="button1" @click="updatestu(stu)">修改</button> |
						<button class="button1" @click="delstu(index)">删除</button>
					</td>
				</tr>
				<tr>
					<td colspan="8">借阅人总数:{{total}}</td>
				</tr>
			</table>
			<table class="table2">
				<td>
					<p>添加/修改借阅者</p>								
					<p>姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:<input type="text" v-model="sname" /></p>
					<p>学&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号:<input type="text" v-model="sno" /></p>
					<p>借阅书本:<input type="text" v-model="bbook" /></p>
					<p>借阅日期:<input type="text" v-model="timeborrow" /></p>
					<p>归还日期:<input type="text" v-model="timereturn" /></p>
					<p>是否归还:<input type="text" v-model="state" /></p>
					
					<p><button class="button1" @click="updstu()">修改</button>|<button class="button1" @click="addstu()">添加</button></p>
					
				</td>
			</table>
		</div>
	</div>
</template>

<script>
	export default {
		name:'borrow',
		data(){
			return{
				flag:false,
				sname:"",
				sno:"",
				bbook:"",
				timeborrow:"",
				timereturn:"",
				state:"",	
				keywords:"",
				stus:[
					{sname:"张三",sno:"2008314210",bbook:"三国演义",timeborrow:"2022-1-3",timereturn:"----",state:"未归还"},
					{sname:"李四",sno:"2008314330",bbook:"水浒传",timeborrow:"2022-1-5",timereturn:"----",state:"未归还"},
					{sname:"胡苗子",sno:"2008312350",bbook:"红楼梦",timeborrow:"2022-1-7",timereturn:"----",state:"未归还"},
					{sname:"王五",sno:"2008312290",bbook:"西游记",timeborrow:"2022-1-28",timereturn:"----",state:"未归还"}
					
				],
			}
		},
		computed:{
			total:function(){
				return this.stus.length;
			},
			
			stus_result(){
				var _this = this;
				if(this.keywords){
					let querystu = this.stus.filter(function(items){
						if(items.sname.indexOf(_this.keywords)>=0 || items.bbook.indexOf(_this.keywords)>=0){
							return true
						}else{
							return false
						}
					})
					return querystu
				}else{
					return this.stus
				}
			}
			
		},
		methods:{
			delstu(index){
				if(window.confirm('确认要删除？')){
					this.stus.splice(index,1);
				}
			},
			updatestu(stu){
				this.updateStu = stu;
				this.sname = this.updateStu.sname;
				this.sno = this.updateStu.sno;
				this.bbook = this.updateStu.bbook;
				this.timeborrow = this.updateStu.timeborrow;
				this.timereturn = this.updateStu.timereturn;
				this.state = this.updateStu.state;
				this.flag = true;
			},
			updstu() {
				this.updateStu.sname = this.sname;
				this.updateStu.sno = this.sno;
				this.updateStu.bbook = this.bbook;
				this.updateStu.timeborrow = this.timeborrow;
				this.updateStu.timereturn = this.timereturn;
				this.updateStu.state = this.state;
				
				this.flag = false;
				this.updateStu = {};
				
				this.sname = '';
				this.sno = '';
				this.bbook = '';
				this.state = '';
			},
			addstu(){
				var newstu = new Object;
				if(this.sname.length != 0 && this.sno.length != 0 && this.bbook.length != 0 && this.state.length != 0 && this.timeborrow.length != 0){
					newstu.sname = this.sname;
					newstu.sno = this.sno;
					newstu.bbook = this.bbook;
					newstu.timeborrow = this.timeborrow;
					newstu.state = this.state;
					
					this.stus.push(newstu);
					
					this.sname="";
					this.sno="";
					this.bbook="";
					this.timeborrow="";
					this.state="";
				}else{
					alert('请填写完整');
				}
			}
			
		}
	}
</script>

<style>
</style>
